button {
  padding: 10px 20px;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  letter-spacing: 1px;
  cursor: pointer;
}

button.alt-primary {
  border: 2px solid #fff;
  background: #fff;
  color: #000;
}

button.alt-secondary {
  border: 2px solid #fff;
  color: white;
  background: none;
}

button:active {
  position: relative;
  top: 1px;
}

* {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

#main {
  background: #000 url(fabian-main.png);
  background-size: auto 100%;
  background-position: 50% -10vh;
  background-repeat: no-repeat;
  min-height: 100vh;
  width: 100%;
}

@media (min-width: 1200px) {
  #main {
    background-position: 50% 50%;
  }
}

#main #text-container {
  position: absolute;
  left: 50%;
  top: auto;
  bottom: 60px;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
}

@media (min-width: 1200px) {
  #main #text-container {
    left: 70%;
    top: 40%;
    bottom: auto;
  }
}

#main #text-container #title-text {
  font-size: 82px;
  letter-spacing: -3px;
  font-weight: 500;
  color: white;
}

@media (min-width: 1200px) {
  #main #text-container #title-text {
    font-size: 128px;
    letter-spacing: -6.8px;
  }
}

#main #text-container #subtitle-text {
  font-size: 35px;
  margin-left: 8px;
  font-weight: 600;
  letter-spacing: -0.66px;
  color: white;
}

@media (min-width: 1200px) {
  #main #text-container #subtitle-text {
    margin-top: -10px;
    font-size: 57.5px;
    margin-left: 10px;
    letter-spacing: -3.05px;
  }
}

#main #action-container {
  position: absolute;
  bottom: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  max-width: 320px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

@media (min-width: 1200px) {
  #main #action-container {
    bottom: 160px;
  }
}

#main #action-container button {
  margin: 0 7px;
  width: 100%;
}

@media (min-width: 480px) {
  #main #action-container button {
    width: auto;
  }
}

